<template>
  <div>
    <b-card
      :title="spelling.spelling"
      class="spelling-card"
      @click="detailed = !detailed">
      <b-collapse v-model="detailed">
        <b-card-text>
          <p>
            <b>Spelling</b><br>
            <span v-html="articulateApproach(spelling.approach)"/><br>
          </p>
          <p>
            <b>Recognition</b><br>
            <span v-html="articulateRecognition(spelling)"/><br>
          </p>
          <p>
            <b>Source</b><br>
            <span v-html="articulateSource(spelling.citation.bibtex)"/>
            <a class="bibtex-link" v-on:click="copyBibtex(spelling.citation.bibtex)">
              copy bibtex citation to clipboard
            </a>
          </p>
        </b-card-text>
      </b-collapse>
    </b-card>
  </div>
</template>

<script>
import {keys, includes} from 'ramda';
const Cite = require('citation-js'); // TODO: see if you can just import function

export default {
  name: 'Spelling',
  props: {
    spelling: Object
  },
  data() {
    return {
      detailed: false,
    }
  },
  methods: {
    articulateApproach: function (approach) {
      let articulations = {
        'current linguistic spelling':
          'A community uses this form devised by linguists.',
        'alternative linguistic spelling':
          'No community currently uses this form devised by linguists.',
        'prior linguistic spelling':
          'No community uses this form devised by linguists any more.',
        'suggested original phonetic form':
          `Linguists suggest this may be the original phonetic form. See the
          source for further detail on the spelling system used.`,
        'suggested original phonetic form with parameters':
          `Linguists have used special characters (like capital letters) in this
          spelling to represent unknown qualities in the original form. See the
          source for further detail.`,
        'linguistically pragmatic spelling' :
          `Linguists have intentionally disregarded certain phonetic details and
          used Australian English spelling patterns in this spelling to
          encourage non-specialists to give as close a rendering to the phonetic
          original as possible.`,
        'naïve':
          `This spelling was recorded by a lay person without linguistic
          training.`,
      };
      if (! includes(approach, keys(articulations))) {
        console.error(`Approach not supported: ${approach}`);
        return '';
      } else {
        return articulations[approach];
      }
    },
    articulateRecognition: function (spelling) {
      let articulations = {
        'true':
          `This name is recognised by the ${spelling.settler_state_recognising_body}.`,
        'under different spelling':
          `This name is recognised under a different spelling by the ${spelling.settler_state_recognising_body}.`,
        'false':
          'This name is not recognised by the Australian settler state.',
      }
      if (! includes(spelling.settler_state_recognition, keys(articulations))) {
        return '';
      } else {
        return articulations[spelling.settler_state_recognition];
      }
    },
    articulateSource: function (citation) {
      let returnCitation;
      try {
        returnCitation = Cite(citation).format('bibliography', {
          format: 'html',
          template: 'harvard',
          lang: 'en-UK'
        });
      } catch (error) {
        returnCitation = citation;
      }
      // return ['This spelling has been cited from: ', returnCitation].join(' ');
      return returnCitation;
    },
    copyBibtex: async function (input) {
      await navigator.clipboard.writeText(input);
    },
  }
}
</script>

