<template>
  <div id="app">

    <div id="map"></div>

    <div id="nav-bar">
      <div id="menu-icon">
        <b-icon-list font-scale="2" v-on:click="displayMenu"/>
      </div>
      <v-select
        label="name"
        :options="options"
        :reduce="options => options.place_identifier"
        v-model="selection"
        placeholder="Enter a settler name…"
      />
    </div>

    <b-card id="search-results" v-if="selection">
      <b-card-text>
        <p>This place was first called:</p>
        <div class="spelling-instance" v-for="spelling in records" :key="spelling.spelling_identifier">
          <spelling :spelling="spelling"/>
        </div>
        <p v-html="parsePeopleNames(peopleNames)"></p>
      </b-card-text>
    </b-card>

    <div id="menu-items" v-if="menuVisible">
      <b-icon-x font-scale="2" v-on:click="hideMenu"/>
      <div id="menu-text">

        <b-card title="Spelling and pronounciation" v-b-toggle.collapse-speak>
          <b-collapse id="collapse-speak">
            <b-card-text>
              <p>
                Some people might find first names intimidating to pronounce, but there's no reason they should be. In fact, standard spellings devised by linguists are actually much easier to pronounce than English spellings, which are devilled with uncertainties and irregularities. If you familiarise yourself with a standard spelling system devised by a linguist, you should be able to make confident pronounciations within a very short amount of time. And if you take just a few minutes to learn a little bit about Australian languages more broadly, you can make educated guesses when you encounter place names in a language you are unfamiliar with.
              </p>
              <p><b>Understanding different spellings</b></p>
              <p>
                There are four primary types of spelling that you might encounter when using this app:
                <ul>
                  <li><b>current spellings devised by linguists</b>: these are spellings that are used by a community and have been devised by a linguist using a standard spelling system; where these are available, you should familiarise yourself with the spelling system and use these where possible</li>
                  <li><b>suggested original forms</b>: these are spellings devised by linguists where there is no longer a community with the knowledge to confirm the pronounciation of the place name; these spellings may include the sounds most likely to have occurred in the name, or placeholders to describe a variety of likely possible sounds</li>
                  <li><b>pragmatic respellings</b>: these are spellings that intentionally disregard phonetic accuracy and instead adopt spelling patterns used in Australian English to give a spelling that would encourage a non-specialist to give as close a rendering to the phonetic original as possible; these have typically been devised by linguists for dual names in places where language continuity has been lost because of colonial violence</li>
                  <li><b>naïve spellings</b>: these are spellings recorded by lay people without linguistic training; typically these are fragments of the historical colonial record, taken down by government officials, missionaries, or other settlers; there is no reason for their use where respellings exist today</li>
                </ul>
              </p>
              <p><b>A brief background on Australian languages</b></p>
              <p>
                Most Australian languages belong to one large family called "Pama-Nyungan" (McGregor 2004: 37). Languages in this family cover the majority of the mainland continent with the exception of the Kimberley, Arnhem Land, and the Torres Strait. Because of the history of colonial violence in Tasmania, linguists do not have enough information to ascertain if the Tasmanian languages belonged to the Pama-Nyungan family.
              </p>
              <p>
                Languages belonging to the same family share similar features, although many common features also spill over family boundaries. Most Australian languages (but not all):
                <ul>
                  <li>have just three vowel sounds: a, u, and i</li>
                  <li>do not distinguish between voiced and unvoiced consonants e.g. b/p, k/g, t/d</li>
                  <li>lack fricatives e.g. f/v, s/z</li>
                  <li>distinguish two types of "r": one trilled or flapped, the other continuant</li>
                  <li>only begin syllables with consonants, not vowels</li>
                  <li>put stress on the first syllable in a word (McGregor 2004: 2, 82–83, 91–92)</li>
                </ul>
              </p>
              <p>
                To learn more about Australian languages, there are a number of good introductory texts:
                <ul>
                  <li>Blake, B., 1981, <i>Australian Aboriginal languages</i>, Sydney: Angus and Robertson.</li>
                  <li>Blake, B., 1987, <i>Australian Aboriginal grammar</i>, Sydney: Croom Helm.</li>
                  <li>Dixon, R. M. W., 1980, <i>The languages of Australia</i>, Cambridge: Cambridge University Press.</li>
                  <li>McGregor, W. B., 2004, <i>The languages of the Kimberley, Western Australia</i>, Routledge.</li>
                  <li>Yallop, C., 1982, <i>Australian Aboriginal languages</i>, London: Andre Deutsch.</li>
                </ul>
                To learn more about a specific language, consult <a href="https://collection.aiatsis.gov.au/austlang/bibliography">AIATSIS's language bibliographies</a>.
              </p>
            </b-card-text>
          </b-collapse>
        </b-card>

        <b-card title="Learn more about Country" v-b-toggle.collapse-learn>
          <b-collapse id="collapse-learn">
            <b-card-text>
              <p>
                Names are powerful symbols, but without context they tell little about a place. You can learn more about places and place names through the following resources:
                <ul>
                  <li>ABC News and contributors, <i><a href="https://iview.abc.net.au/show/this-place">This Place</a></i></li>
                  <li>Attenbrow, V 2010, <i>Sydney’s Aboriginal past : investigating the archaeological and historical records</i> 2nd ed., University of New South Wales Press, Sydney, N.S.W.</li>
                  <li>City of Sydney, <a href="https://www.sydneybarani.com.au">Sydney barani</a></li>
                  <li>Clark, I. D., Hercus, L., and Kostanski, L., 2014, <i>Indigenous and minority placenames: Australian and international perspectives</i>, ANU Press, Canberra.</li>
                  <li>Eidelson, M., 2014, <i>Melbourne Dreaming: a guide to important places of the past and present</i>, Aboriginal Studies Press.</li>
                  <li>Hercus, L., Hodges, F., and Simpson, J., 2009, <i>The land is a map: placenames of Indigenous origin in Australia</i>, ANU Press, Canberra.</li>
                  <li>Hinkson, M., and Harris, A., 2010, <i>Aboriginal Sydney: a guide to important places of the past and present</i>, Aboriginal Studies Press.</li>
                  <li>Koch, H., and Hercus, L., 2009, <i>Aboriginal Placenames: naming and re-naming the Australian landscape</i>, ANU Press, Canberra.</li>
                </ul>
              </p>
            </b-card-text>
          </b-collapse>
        </b-card>

        <b-card title="About this app" v-b-toggle.collapse-about>
          <b-collapse id="collapse-about">
            <b-card-text>
              <p><b>Status</b></p>
              <p>
                This app is being developed as a concept to help settler Australians remember the first names of Country. The content of the app may be inaccurate and incomplete and has not been peer reviewed. More importantly, no consultation has been undertaken with the First Nations of the Country that this app records names for: for this reason, this app should be used cautiously without further guidance. Consultation with First Nations is intended to inform the future direction of this app (including potentially discontinuing the app). Ownership of this app is ultimately intended to be transferred to First Nations.
              </p>
              <p><b>Purpose</b></p>
              <p>
                This app is designed to help settler Australians remember the first names of Country that they might have forgotten or tried to erase.
              </p>
              <p>
                This app is <i>not</i> an authority on First Nations knowledges.
              </p>
              <p><b>Spellings</b></p>
              <p>
                This app provides numerous spellings for some names for two reasons. Firstly, linguistically derived spellings (rather than naïve colonial spellings) are provided to help settler Australians speak these names as First Nations might have spoken these names in the past, or as First Nations still speak these names today. Secondly, the multitude of spellings is provided to remind settler Australians of the history of this place: settler Australians' disinterest and destruction of First Nations lives, languages, and cultures, has left large gaps in the historical record that may never be filled, and uncertainties that may never be resolved.
              </p>
              <p>
                If settler Austrlaians find themselves ignorant of a placename close to home, unsure of which respelling to choose, or how to pronounce it, it might prompt them to ask questions about this place's history, and take action to right past wrongs.
              </p>
              <p><b>Locations</b></p>
              <p>
                Most places in this app are identified by a point on the map. The map and the points on it are intended to help settler Australians discover place names by browsing over an area. It is important to stress that these points are indicative only. They do not fully describe a place, nor may they fully describe First Nations understandings of that place. Furthermore, in some cases they may represent an interpretation of a colonial record which may be unclear or incomplete; for this reason, some points may be considered an interpretation at this point in time, and may be open to reinterpretation in future.
              </p>
              <p><b>Authorship</b></p>
              <p>
                Place names should be considered Indigenous Cultural and Intellectual Property (ICIP).
              </p>
              <p>
                This app has been developed primarily by D'Arcy Roche, a barawalgal (in Sydney language: a person from far away) living on Derrawunn [DVRawAn] on Gadigal Country. D'Arcy has also received helpful input from Ed Wensing. There are plans to open authorship and ownership of this app in future.
              </p>
              <p>
                Several important works which draw together historical sources have been drawn on here to compile the database. These include:
                <ul>
                  <li>Attenbrow, V., 2009, <i>Sydney's Aboriginal past : investigating the archaeological and historical records</i></li>
                  <li>Troy, J., 1994, <i>The Sydney Language</i></li>
                </ul>
              </p>
            </b-card-text>
          </b-collapse>
        </b-card>

      </div>
    </div>
  </div>
</template>

<script>
// CSS
import 'bootstrap/dist/css/bootstrap.css'
import 'bootstrap-vue/dist/bootstrap-vue.css'
import 'vue-select/dist/vue-select.css'
import 'leaflet/dist/leaflet.css'
// JS libraries
import { BootstrapVue, IconsPlugin } from 'bootstrap-vue'
import filter from 'lodash/fp/filter'
import flow from 'lodash/fp/flow'
import forEach from 'lodash/fp/forEach'
import map from 'lodash/fp/map'
import orderBy from 'lodash/fp/orderBy'
import pick from 'lodash/fp/pick'
import L from 'leaflet'
import vSelect from 'vue-select'
import Vue from 'vue'
// data (JS objects)
import peopleInformation from '@/assets/people_information.json'
import pointLocations from '@/assets/point_locations.json'
import settlerNames from '@/assets/settler_names.json'
import spellings from '@/assets/spellings.json'
import Spelling from '@/components/Spelling.vue'

Vue.use(BootstrapVue)
Vue.use(IconsPlugin)
Vue.component('v-select', vSelect)

// citation functions
const Cite = require('citation-js')
const cite = function(bibtexString) {
  try {
    let parsedCitation = Cite(bibtexString).format('bibliography', {
      format: 'html',
      template: 'harvard',
      lang: 'en-UK'
    });
    return {parsed_citation: parsedCitation, bibtex: bibtexString};
  } catch (error) {
    return {parsed_citation: '', bibtex: bibtexString};
  }
}

export default {
  name: 'App',
  components: {
    vSelect,
    Spelling,
  },
  data() {
    return {
      menuVisible: false,
      approachOrdering: {
        'current linguistic spelling': 1,
        'prior linguistic spelling': 2,
        'suggested original phonetic form': 3,
        'suggested original phonetic form with parameters': 4,
        'linguistically pragmatic spelling': 5,
        'naïve': 6
      },
      columnOrdering: [
        'spelling', 'approach', 'settler_state_recognition',
        'settler_state_recognising_body', 'citation',
      ],
      // columns to display in the table of first names spellings
      displayColumns: [
        'spelling', 'approach', 'settler_state_recognition',
        'settler_state_recognising_body', 'citation',
      ],
      fallBackCoordinates: [-33.88936326971, 151.2027401254],
      // how to display points on the map
      markerOptions: {
        radius: 6,
        fillColor: "#ff7800",
        weight: 0,
        fillOpacity: 0.8
      },
      map: null,
      // the place selected from the select input
      selection: null,
      // the spatial data to display on the map
      spatialData: pointLocations,
      // the base map layer
      tileLayer: null
    }
  },
  computed: {
    // set the map focus on the user's location, if possible
    userLocation: function() {
      let coords;
      const geolocationPermitted = function(position) {
        coords = [position.coords.latitude, position.coords.longitude];
      }
      if (navigator.geolocation) {
        navigator.geolocation.getCurrentPosition(
          geolocationPermitted,
          () => {},
          {timeout: 10000}
        );
      }
      return coords;
    },
    placeCoordinates: function() {
      if (this.selection) {
        let filteredRows = filter(
          {'place_identifier': this.selection},
          this.spatialData
        );
        if (filteredRows[0].latitude) {
          let coordinates = filteredRows[0]; // TODO: fix this dodgy accessing
          return [coordinates.latitude, coordinates.longitude];
        } else {
          return null;
        }
      } else return null;
    },
    mapViewCoordinates: function() {
      if (this.placeCoordinates) {
        let vh = window.innerHeight;
        let vw = window.innerWidth;
        let rem = parseFloat(getComputedStyle(document.documentElement).fontSize);
        let targetLatLng;
        // define different zoom adjustments for different screen sizes
        if (vw > 40 * rem) {
          let searchResultsWidth = 25 * rem; // TODO: access this value dynamically, not with a magic number
          let targetPoint = this.map.project(this.placeCoordinates, 14).subtract([searchResultsWidth / 2, 0]);
          targetLatLng = this.map.unproject(targetPoint, 14);
        } else {
          let searchResultsHeight = vh / 2;
          let targetPoint = this.map.project(this.placeCoordinates, 14).add([0, searchResultsHeight / 2]);
          targetLatLng = this.map.unproject(targetPoint, 14);
        }
        return targetLatLng;
      } else return this.fallBackCoordinates;
    },
    // order the settler names alphabetically to display in the select input
    options: function() {
      return orderBy(['name'], ['asc'], settlerNames);
    },
    // first name spellings to display in a table
    records: function() {
      return flow(
        filter({'place_identifier' : this.selection}),
        orderBy(
          [o => this.approachOrdering[o.approach], 'settler_state_recognition', 'spelling'],
          ['asc', 'desc', 'asc']
        ),
        forEach(o => o.citation = cite(o.bibtex_citation)),
        map(pick(this.displayColumns))
      )(spellings);
    },
    popupContent: function() {
      return map('spelling', this.records).join('; ')
    },
    peopleNames: function() {
      return flow(
        filter({'place_identifier' : this.selection})
      )(peopleInformation);
    },
  },
  methods: {
    displayMenu() {
      this.menuVisible = true;
    },
    hideMenu() {
      this.menuVisible = false;
    },
    jumpTo: function (id) {
      document.getElementById(id).scrollIntoView();
    },
    copyBibtex: async function (input) {
      await navigator.clipboard.writeText(input);
    },
    // create the map with the base tile layer
    initMap() {
      this.map = L.map('map', {
        center: this.userLocation? this.userLocation : this.fallBackCoordinates,
        zoom: 10,
        zoomControl: false,
      });
      this.tileLayer = L.tileLayer(
        'https://{s}.basemaps.cartocdn.com/light_all/{z}/{x}/{y}{r}.png',
        {
          attribution: [
            '&copy; <a href="http://www.openstreetmap.org/copyright">OpenStreetMap</a>,',
            '&copy; <a href="https://carto.com/attribution">CARTO</a>',
            '| placenames are ICIP',
          ].join(' '),
          maxZoom: 19,
          subdomains: 'abcd'
        }
      );
      this.tileLayer.addTo(this.map);
    },
    initLayers() {
      // only work with those rows with a latitude
      let applicableRows = filter('latitude', this.spatialData);
      applicableRows.forEach((feature) => {
        L.circleMarker([feature.latitude, feature.longitude], this.markerOptions)
          .on('click', () => {
            this.selection = feature.place_identifier;
          })
          .addTo(this.map);
      });
    },
    zoomMapWithAdjustments: function() {
      let vh = window.innerHeight;
      let vw = window.innerWidth;
      let rem = parseFloat(getComputedStyle(document.documentElement).fontSize);
      if (vw > 40 * rem) {
        let searchResultsWidth = 25 * rem; // TODO: access this value dynamically, not with a magic number
        let targetPoint = this.map.project(this.mapViewCoordinates, 14).subtract([searchResultsWidth / 2, 0]);
        let targetLatLng = this.map.unproject(targetPoint, 14);
        this.map.setView(targetLatLng, 14);
      } else {
        let searchResultsHeight = vh / 2;
        let targetPoint = this.map.project(this.mapViewCoordinates, 14).add([0, searchResultsHeight / 2]);
        let targetLatLng = this.map.unproject(targetPoint, 14);
        this.map.setView(targetLatLng, 14);
      }
    },
    parsePeopleNames: function(allNames) {
      let names = filter(o => o.certainty === 'certain', allNames);
      let parsed;
      if (names.length === 0) {
        parsed = null;
      } else {
        parsed = `
          This is ${map(o => o.people_identifier, names).join(' and ')} Country.
        `;
      }
      return parsed;
    },
  },
  mounted() {
    this.initMap();
    this.initLayers();
  },
  watch: {
    selection: function() {
      // if no geometry
      if (! this.placeCoordinates) {
        this.map.closePopup();
      } else {
        this.map.setView(this.mapViewCoordinates, 14);
        L.popup()
          .setLatLng(this.placeCoordinates)
          .setContent(this.popupContent)
          .openOn(this.map);
      }
    }
  }
}
</script>

